'use client';

import React, { createContext, useContext, useReducer, useEffect } from 'react';
import { Product } from '@/types';
import { apiClient } from '@/lib/api';

interface ProductState {
  products: Product[];
  loading: boolean;
  error: string | null;
  appointments: any[];
  orders: any[];
}

interface ProductAction {
  type: string;
  payload?: any;
}

// Action types
const PRODUCT_ACTIONS = {
  SET_PRODUCTS: 'SET_PRODUCTS',
  SET_LOADING: 'SET_LOADING',
  SET_ERROR: 'SET_ERROR',
  SET_APPOINTMENTS: 'SET_APPOINTMENTS',
  SET_ORDERS: 'SET_ORDERS',
  ADD_ORDER: 'ADD_ORDER'
};

// Initial state
const initialState: ProductState = {
  products: [],
  loading: false,
  error: null,
  appointments: [],
  orders: []
};

// Reducer
const productReducer = (state: ProductState, action: ProductAction): ProductState => {
  switch (action.type) {
    case PRODUCT_ACTIONS.SET_PRODUCTS:
      return {
        ...state,
        products: action.payload
      };
    case PRODUCT_ACTIONS.SET_LOADING:
      return {
        ...state,
        loading: action.payload
      };
    case PRODUCT_ACTIONS.SET_ERROR:
      return {
        ...state,
        error: action.payload
      };
    case PRODUCT_ACTIONS.SET_APPOINTMENTS:
      return {
        ...state,
        appointments: action.payload
      };
    case PRODUCT_ACTIONS.SET_ORDERS:
      return {
        ...state,
        orders: action.payload
      };
    case PRODUCT_ACTIONS.ADD_ORDER:
      return {
        ...state,
        orders: [action.payload, ...state.orders]
      };
    default:
      return state;
  }
};

// Create context
export const ProductContext = createContext<{
  products: Product[];
  loading: boolean;
  error: string | null;
  appointments: any[];
  orders: any[];
  setProducts: (products: Product[]) => void;
  setLoading: (loading: boolean) => void;
  setError: (error: string | null) => void;
  setAppointments: (appointments: any[]) => void;
  setOrders: (orders: any[]) => void;
  fetchProducts: () => Promise<void>;
  fetchProductBySlug: (slug: string) => Promise<Product>;
  searchProducts: (params: {
    q?: string;
    search?: string;
    category?: string;
    minPrice?: number;
    maxPrice?: number;
    page?: number;
    limit?: number;
  }) => Promise<any>;
  fetchOrders: () => Promise<void>;
  createOrder: (orderData: any) => Promise<any>;
  createProduct: (productData: any) => Promise<any>;
  updateProduct: (id: number, productData: any) => Promise<any>;
  fetchAppointments: (filters?: {
    status?: string;
    date?: string;
    page?: number;
    limit?: number;
  }) => Promise<any>;
  createAppointment: (appointmentData: {
    appointment_date: string;
    notes?: string;
  }) => Promise<any>;
  updateAppointment: (id: string, updateData: {
    status?: string;
    notes?: string;
  }) => Promise<any>;
} | undefined>(undefined);

// Provider component
interface ProductProviderProps {
  children: React.ReactNode;
}

export const ProductProvider: React.FC<ProductProviderProps> = ({ children }) => {
  const [state, dispatch] = useReducer(productReducer, initialState);

  // Actions
  const setProducts = (products: Product[]) => {
    dispatch({ type: PRODUCT_ACTIONS.SET_PRODUCTS, payload: products });
  };

  const setLoading = (loading: boolean) => {
    dispatch({ type: PRODUCT_ACTIONS.SET_LOADING, payload: loading });
  };

  const setError = (error: string | null) => {
    dispatch({ type: PRODUCT_ACTIONS.SET_ERROR, payload: error });
  };

  const setAppointments = (appointments: any[]) => {
    dispatch({ type: PRODUCT_ACTIONS.SET_APPOINTMENTS, payload: appointments });
  };

  const setOrders = (orders: any[]) => {
    dispatch({ type: PRODUCT_ACTIONS.SET_ORDERS, payload: orders });
  };

  // Fetch products
  const fetchProducts = async () => {
    // Don't fetch if already loading to prevent race conditions
    if (state.loading) return;

    setLoading(true);
    setError(null);

    try {
      const response = await apiClient.getProducts();
      if (response.success && response.data) {
        // Handle the new response structure with pagination
        const productsData = Array.isArray(response.data) ? response.data : response.data?.products || [];

        // Convert price strings to numbers for proper formatting
        // Map mock image_url identifiers to appropriate imageClass values
        const mapImageIdentifierToClass = (identifier: string) => {
          if (!identifier) return 'modern';

          const lowerIdentifier = identifier.toLowerCase();
          if (lowerIdentifier.includes('modern') || lowerIdentifier.includes('coffee') || lowerIdentifier.includes('sofa')) {
            return 'modern';
          } else if (lowerIdentifier.includes('classic') || lowerIdentifier.includes('armchair')) {
            return 'classic';
          } else if (lowerIdentifier.includes('vintage') || lowerIdentifier.includes('lamp')) {
            return 'coastal'; // Using coastal for lighting items
          } else if (lowerIdentifier.includes('dining') || lowerIdentifier.includes('dresser')) {
            return 'office'; // Using office for furniture items
          } else if (lowerIdentifier.includes('wall') || lowerIdentifier.includes('art')) {
            return 'hotel'; // Using hotel for decor items
          } else if (lowerIdentifier.includes('rug')) {
            return 'restaurant'; // Using restaurant for decor items
          } else {
            return 'modern'; // default
          }
        };

        const formattedProducts = productsData.map((product: any) => ({
          ...product,
          price: typeof product.price === 'number' ? product.price : parseFloat(product.price),
          // Only set image_url to undefined if it's not a proper URL
          // If it looks like an actual URL (contains http), keep it
          image_url: product.image_url && (product.image_url.startsWith('http') || product.image_url.startsWith('/'))
            ? product.image_url
            : undefined,
          // Use the identifier to determine the appropriate CSS class
          imageClass: mapImageIdentifierToClass(product.image_url) || product.imageClass || 'modern'
        }));
        dispatch({ type: PRODUCT_ACTIONS.SET_PRODUCTS, payload: formattedProducts });
      } else {
        throw new Error(response.error || 'Failed to load products');
      }
    } catch (error: any) {
      console.error('Error fetching products:', error);
      setError(error.message || 'Failed to load products. Please try again later.');
    } finally {
      setLoading(false);
    }
  };

  // Fetch product by slug
  const fetchProductBySlug = async (slug: string): Promise<Product> => {
    try {
      const response = await apiClient.getProductBySlug(slug);
      if (response.success && response.data) {
        return response.data;
      } else {
        throw new Error(response.error || 'Failed to load product');
      }
    } catch (error: any) {
      console.error('Error fetching product by slug:', error);
      setError(error.message || 'Failed to load product. Please try again later.');
      throw error;
    }
  };

  // Search products
  const searchProducts = async (params: {
    q?: string;
    search?: string;
    category?: string;
    minPrice?: number;
    maxPrice?: number;
    page?: number;
    limit?: number;
  }) => {
    setLoading(true);
    setError(null);

    try {
      const response = await apiClient.searchProducts(params);
      if (response.success && response.data) {
        // Format the products similar to fetchProducts
        const productsData = Array.isArray(response.data.products) ? response.data.products : response.data;
        
        const formattedProducts = Array.isArray(productsData) 
          ? productsData.map((product: any) => ({
              ...product,
              price: typeof product.price === 'number' ? product.price : parseFloat(product.price),
              image_url: product.image_url && (product.image_url.startsWith('http') || product.image_url.startsWith('/'))
                ? product.image_url
                : undefined,
            }))
          : [];

        dispatch({ type: PRODUCT_ACTIONS.SET_PRODUCTS, payload: formattedProducts });
        return response.data;
      } else {
        throw new Error(response.error || 'Failed to search products');
      }
    } catch (error: any) {
      console.error('Error searching products:', error);
      setError(error.message || 'Failed to search products. Please try again later.');
      throw error;
    } finally {
      setLoading(false);
    }
  };

  // Fetch orders
  const fetchOrders = async () => {
    setLoading(true);
    setError(null);

    try {
      const response = await apiClient.getOrders();
      if (response.success && response.data) {
        dispatch({ type: PRODUCT_ACTIONS.SET_ORDERS, payload: response.data });
        return response.data;
      } else {
        throw new Error(response.error || 'Failed to load orders');
      }
    } catch (error: any) {
      console.error('Error fetching orders:', error);
      setError(error.message || 'Failed to load orders. Please try again later.');
      throw error;
    } finally {
      setLoading(false);
    }
  };

  // Create order
  const createOrder = async (orderData: any) => {
    setLoading(true);
    setError(null);

    try {
      const response = await apiClient.createOrder(orderData);
      if (response.success && response.data) {
        dispatch({ type: PRODUCT_ACTIONS.ADD_ORDER, payload: response.data });
        return response.data;
      } else {
        throw new Error(response.error || 'Failed to create order');
      }
    } catch (error: any) {
      console.error('Error creating order:', error);
      setError(error.message || 'Failed to create order. Please try again.');
      throw error;
    } finally {
      setLoading(false);
    }
  };

  // Create product
  const createProduct = async (productData: any) => {
    setLoading(true);
    setError(null);

    try {
      const response = await apiClient.createProduct(productData);
      if (response.success && response.data) {
        // Refresh the products list
        await fetchProducts();
        return response.data;
      } else {
        throw new Error(response.error || 'Failed to create product');
      }
    } catch (error: any) {
      console.error('Error creating product:', error);
      setError(error.message || 'Failed to create product. Please try again.');
      throw error;
    } finally {
      setLoading(false);
    }
  };

  // Update product
  const updateProduct = async (id: number, productData: any) => {
    setLoading(true);
    setError(null);

    try {
      const response = await apiClient.updateProduct(id, productData);
      if (response.success && response.data) {
        // Refresh the products list
        await fetchProducts();
        return response.data;
      } else {
        throw new Error(response.error || 'Failed to update product');
      }
    } catch (error: any) {
      console.error('Error updating product:', error);
      setError(error.message || 'Failed to update product. Please try again.');
      throw error;
    } finally {
      setLoading(false);
    }
  };

  // Fetch appointments
  const fetchAppointments = async (filters?: {
    status?: string;
    date?: string;
    page?: number;
    limit?: number;
  }) => {
    setLoading(true);
    setError(null);

    try {
      const response = await apiClient.getAppointments(filters);
      if (response.success && response.data) {
        dispatch({ type: PRODUCT_ACTIONS.SET_APPOINTMENTS, payload: response.data });
        return response.data;
      } else {
        throw new Error(response.error || 'Failed to load appointments');
      }
    } catch (error: any) {
      console.error('Error fetching appointments:', error);
      setError(error.message || 'Failed to load appointments. Please try again later.');
      throw error;
    } finally {
      setLoading(false);
    }
  };

  // Create appointment
  const createAppointment = async (appointmentData: {
    appointment_date: string;
    notes?: string;
  }) => {
    setLoading(true);
    setError(null);

    try {
      const response = await apiClient.createAppointment(appointmentData);
      if (response.success && response.data) {
        // Refresh appointments list
        await fetchAppointments();
        return response.data;
      } else {
        throw new Error(response.error || 'Failed to create appointment');
      }
    } catch (error: any) {
      console.error('Error creating appointment:', error);
      setError(error.message || 'Failed to create appointment. Please try again.');
      throw error;
    } finally {
      setLoading(false);
    }
  };

  // Update appointment
  const updateAppointment = async (id: string, updateData: {
    status?: string;
    notes?: string;
  }) => {
    setLoading(true);
    setError(null);

    try {
      const response = await apiClient.updateAppointment(id, updateData);
      if (response.success && response.data) {
        // Refresh appointments list
        await fetchAppointments();
        return response.data;
      } else {
        throw new Error(response.error || 'Failed to update appointment');
      }
    } catch (error: any) {
      console.error('Error updating appointment:', error);
      setError(error.message || 'Failed to update appointment. Please try again.');
      throw error;
    } finally {
      setLoading(false);
    }
  };

  return (
    <ProductContext.Provider
      value={{
        products: state.products,
        loading: state.loading,
        error: state.error,
        appointments: state.appointments,
        orders: state.orders,
        setProducts,
        setLoading,
        setError,
        setAppointments,
        setOrders,
        fetchProducts,
        fetchProductBySlug,
        searchProducts,
        fetchOrders,
        createOrder,
        createProduct,
        updateProduct,
        fetchAppointments,
        createAppointment,
        updateAppointment
      }}
    >
      {children}
    </ProductContext.Provider>
  );
};

export const useProduct = () => {
  const context = useContext(ProductContext);
  if (context === undefined) {
    throw new Error('useProduct must be used within a ProductProvider');
  }
  return context;
};