'use client';

import React, { createContext, useContext, useReducer, useEffect } from 'react';
import { User } from '@/types';
import { getCurrentUser, onAuthStateChange, getUserProfile } from '@/lib/supabase/auth';

interface AuthState {
  user: User | null;
  token: string | null;
  loading: boolean;
  error: string | null;
}

interface AuthAction {
  type: string;
  payload?: any;
}

// Action types
const AUTH_ACTIONS = {
  SET_USER: 'SET_USER',
  SET_TOKEN: 'SET_TOKEN',
  SET_LOADING: 'SET_LOADING',
  SET_ERROR: 'SET_ERROR',
  LOGIN: 'LOGIN',
  LOGOUT: 'LOGOUT'
};

// Initial state
const initialState: AuthState = {
  user: null,
  token: null,
  loading: false,
  error: null
};

// Reducer
const authReducer = (state: AuthState, action: AuthAction): AuthState => {
  switch (action.type) {
    case AUTH_ACTIONS.SET_USER:
      return {
        ...state,
        user: action.payload
      };
    case AUTH_ACTIONS.SET_TOKEN:
      return {
        ...state,
        token: action.payload
      };
    case AUTH_ACTIONS.SET_LOADING:
      return {
        ...state,
        loading: action.payload
      };
    case AUTH_ACTIONS.SET_ERROR:
      return {
        ...state,
        error: action.payload
      };
    case AUTH_ACTIONS.LOGOUT:
      return {
        ...state,
        user: null,
        token: null
      };
    default:
      return state;
  }
};

// Create context
export const AuthContext = createContext<{
  user: User | null;
  token: string | null;
  loading: boolean;
  error: string | null;
  setUser: (user: User | null) => void;
  setToken: (token: string | null) => void;
  setLoading: (loading: boolean) => void;
  setError: (error: string | null) => void;
  login: (credentials: any) => Promise<any>;
  register: (userData: any) => Promise<any>;
  logout: () => void;
  verifyToken: () => Promise<boolean>;
  fetchUserProfile: () => Promise<any>;
  updateUserProfile: (profileData: any) => Promise<any>;
} | undefined>(undefined);

// Provider component
interface AuthProviderProps {
  children: React.ReactNode;
}

export const AuthProvider: React.FC<AuthProviderProps> = ({ children }) => {
  const [state, dispatch] = useReducer(authReducer, initialState);

  // Load user from Supabase session on initial render
  useEffect(() => {
    let isMounted = true;
    setLoading(true);

    const initializeAuth = async () => {
      try {
        const supabaseUser = await getCurrentUser();
        if (isMounted && supabaseUser) {
          const userProfile = await getUserProfile();
          if (userProfile) {
            const userObject = {
              id: userProfile.id,
              name: supabaseUser.user_metadata?.full_name || supabaseUser.user_metadata?.name || supabaseUser.email?.split('@')[0] || 'User',
              email: supabaseUser.email,
              avatar: supabaseUser.user_metadata?.avatar || supabaseUser.user_metadata?.picture || null,
              role: userProfile.role,
              created_at: supabaseUser.created_at
            };
            dispatch({ type: AUTH_ACTIONS.SET_USER, payload: userObject });
            localStorage.setItem('user', JSON.stringify(userObject));
          } else {
            const userObject = {
              id: supabaseUser.id,
              name: supabaseUser.user_metadata?.full_name || supabaseUser.user_metadata?.name || supabaseUser.email?.split('@')[0] || 'User',
              email: supabaseUser.email,
              avatar: supabaseUser.user_metadata?.avatar || supabaseUser.user_metadata?.picture || null,
              role: 'customer',
              created_at: supabaseUser.created_at
            };
            dispatch({ type: AUTH_ACTIONS.SET_USER, payload: userObject });
            localStorage.setItem('user', JSON.stringify(userObject));
          }
        } else if (isMounted) {
          localStorage.removeItem('user');
        }
      } catch (error) {
        console.error('Error initializing auth with Supabase:', error);
        if (isMounted) {
          localStorage.removeItem('user');
          dispatch({ type: AUTH_ACTIONS.SET_USER, payload: null });
        }
      } finally {
        if (isMounted) {
          setLoading(false);
        }
      }
    };

    initializeAuth();

    const unsubscribe = onAuthStateChange(async (event, session) => {
      if (isMounted && event === 'SIGNED_IN' && session?.user) {
        const userProfile = await getUserProfile();
        if (userProfile) {
          const userObject = {
            id: userProfile.id,
            name: session.user.user_metadata?.full_name || session.user.user_metadata?.name || session.user.email?.split('@')[0] || 'User',
            email: session.user.email,
            avatar: session.user.user_metadata?.avatar || session.user.user_metadata?.picture || null,
            role: userProfile.role,
            created_at: session.user.created_at
          };
          dispatch({ type: AUTH_ACTIONS.SET_USER, payload: userObject });
          localStorage.setItem('user', JSON.stringify(userObject));
        } else {
          const userObject = {
            id: session.user.id,
            name: session.user.user_metadata?.full_name || session.user.user_metadata?.name || session.user.email?.split('@')[0] || 'User',
            email: session.user.email,
            avatar: session.user.user_metadata?.avatar || session.user.user_metadata?.picture || null,
            role: 'customer',
            created_at: session.user.created_at
          };
          dispatch({ type: AUTH_ACTIONS.SET_USER, payload: userObject });
          localStorage.setItem('user', JSON.stringify(userObject));
        }
      } else if (isMounted && event === 'SIGNED_OUT') {
        localStorage.removeItem('user');
        dispatch({ type: AUTH_ACTIONS.SET_USER, payload: null });
      }
    });

    return () => {
      isMounted = false;
      if (unsubscribe) {
        unsubscribe();
      }
    };
  }, []);

  const setUser = (user: User | null) => {
    dispatch({ type: AUTH_ACTIONS.SET_USER, payload: user });
    if (user) {
      localStorage.setItem('user', JSON.stringify(user));
    } else {
      localStorage.removeItem('user');
    }
  };

  const setToken = (token: string | null) => {
    dispatch({ type: AUTH_ACTIONS.SET_TOKEN, payload: token });
    if (token) {
      localStorage.setItem('token', token);
    } else {
      localStorage.removeItem('token');
    }
  };

  const setLoading = (loading: boolean) => {
    dispatch({ type: AUTH_ACTIONS.SET_LOADING, payload: loading });
  };

  const setError = (error: string | null) => {
    dispatch({ type: AUTH_ACTIONS.SET_ERROR, payload: error });
  };

  const login = async (credentials: any) => {
    // Implementation would go here
    console.log('Login functionality');
  };

  const register = async (userData: any) => {
    // Implementation would go here
    console.log('Register functionality');
  };

  const logout = () => {
    localStorage.removeItem('user');
    localStorage.removeItem('cartItems');
    dispatch({ type: AUTH_ACTIONS.SET_USER, payload: null });
    // Dispatch logout action if needed
  };

  const verifyToken = async () => {
    // Implementation would go here
    return true;
  };

  const fetchUserProfile = async () => {
    // Implementation would go here
    return null;
  };

  const updateUserProfile = async (profileData: any) => {
    // Implementation would go here
    return null;
  };

  return (
    <AuthContext.Provider
      value={{
        user: state.user,
        token: state.token,
        loading: state.loading,
        error: state.error,
        setUser,
        setToken,
        setLoading,
        setError,
        login,
        register,
        logout,
        verifyToken,
        fetchUserProfile,
        updateUserProfile
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};