'use client';

import React from 'react';
import { AuthProvider } from './AuthContext';
import { CartProvider } from './CartContext';
import { ProductProvider } from './ProductContext';
import { UIProvider } from './UIContext';
import { useAuth } from './AuthContext';

// Component that uses the auth context to pass user ID to the cart provider
const CartProviderWrapper: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const { user } = useAuth();
  return <CartProvider userId={user?.id}>{children}</CartProvider>;
};

// Main provider that composes all individual context providers
export const AppProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  return (
    <UIProvider>
      <AuthProvider>
        <ProductProvider>
          <CartProviderWrapper>
            {children}
          </CartProviderWrapper>
        </ProductProvider>
      </AuthProvider>
    </UIProvider>
  );
};