'use client';

import React, { createContext, useContext, useReducer, useEffect, useRef } from 'react';
import { CartItem } from '@/types';
import { apiClient } from '@/lib/api';

interface CartState {
  items: CartItem[];
  loading: boolean;
  error: string | null;
}

interface CartAction {
  type: string;
  payload?: any;
}

// Action types
const CART_ACTIONS = {
  SET_ITEMS: 'SET_ITEMS',
  SET_LOADING: 'SET_LOADING',
  SET_ERROR: 'SET_ERROR',
  ADD_ITEM: 'ADD_ITEM',
  UPDATE_ITEM: 'UPDATE_ITEM',
  REMOVE_ITEM: 'REMOVE_ITEM',
  CLEAR_CART: 'CLEAR_CART'
};

// Initial state
const initialState: CartState = {
  items: [],
  loading: false,
  error: null
};

// Reducer
const cartReducer = (state: CartState, action: CartAction): CartState => {
  switch (action.type) {
    case CART_ACTIONS.SET_ITEMS:
      return {
        ...state,
        items: action.payload
      };
    case CART_ACTIONS.SET_LOADING:
      return {
        ...state,
        loading: action.payload
      };
    case CART_ACTIONS.SET_ERROR:
      return {
        ...state,
        error: action.payload
      };
    case CART_ACTIONS.ADD_ITEM:
      const existingItem = state.items.find(item => item.product_id === action.payload.product_id);
      let updatedItems: CartItem[];

      if (existingItem) {
        updatedItems = state.items.map(item =>
          item.product_id === action.payload.product_id ? action.payload : item
        );
      } else {
        updatedItems = [...state.items, action.payload];
      }

      return {
        ...state,
        items: updatedItems
      };
    case CART_ACTIONS.UPDATE_ITEM:
      return {
        ...state,
        items: state.items.map(item =>
          item.product_id === action.payload.product_id
            ? { ...item, quantity: action.payload.quantity }
            : item
        )
      };
    case CART_ACTIONS.REMOVE_ITEM:
      return {
        ...state,
        items: state.items.filter(item => item.product_id !== action.payload)
      };
    case CART_ACTIONS.CLEAR_CART:
      return {
        ...state,
        items: []
      };
    default:
      return state;
  }
};

// Create context
export const CartContext = createContext<{
  items: CartItem[];
  loading: boolean;
  error: string | null;
  setItems: (items: CartItem[]) => void;
  setLoading: (loading: boolean) => void;
  setError: (error: string | null) => void;
  fetchItems: () => Promise<void>;
  addToCart: (product: any, quantity?: number) => Promise<void>;
  updateItem: (productId: number, quantity: number) => Promise<void>;
  removeItem: (productId: number) => Promise<void>;
  clearCart: () => Promise<void>;
  cartCount: number;
  cartTotal: number;
} | undefined>(undefined);

// Provider component
interface CartProviderProps {
  children: React.ReactNode;
  userId?: string | null; // Pass user ID to determine if user is authenticated
}

export const CartProvider: React.FC<CartProviderProps> = ({ children, userId }) => {
  const [state, dispatch] = useReducer(cartReducer, initialState);
  const tempSavedCart = useRef<CartItem[] | null>(null);
  const ongoingCartRequests = new Set<number>(); // Track ongoing cart requests

  // Calculate cart count and total
  const cartCount = state.items.reduce((total, item) => total + (item.quantity || 0), 0);
  const cartTotal = state.items.reduce((total, item) => total + ((item.price || 0) * (item.quantity || 0)), 0);

  // Fetch cart items
  const fetchItems = async () => {
    if (!userId) {
      // For guest users, always set cart to empty
      dispatch({ type: CART_ACTIONS.SET_ITEMS, payload: [] });
      // Sync Zustand cart with empty array
      import('@/store/cartStore').then((module) => {
        module.useCartStore.getState().clearCart();
      });
      return;
    }

    setLoading(true);
    setError(null);

    try {
      const response = await apiClient.getCartItems();
      if (response.success && response.data) {
        dispatch({ type: CART_ACTIONS.SET_ITEMS, payload: response.data });
        // Sync Zustand cart with fetched data
        import('@/store/cartStore').then((module) => {
          module.useCartStore.getState().clearCart();
          response.data.forEach(item => {
            module.useCartStore.getState().addItem(item);
          });
        });
      } else {
        // Don't throw an error for empty cart, just log and handle gracefully
        console.warn('No cart items found for user:', response.error);
        dispatch({ type: CART_ACTIONS.SET_ITEMS, payload: [] });
        // Sync Zustand cart with empty array
        import('@/store/cartStore').then((module) => {
          module.useCartStore.getState().clearCart();
        });
      }
    } catch (error: any) {
      console.error('Error fetching cart items:', error);
      setError(error.message || 'Failed to load cart items. Please try again later.');
      // Still set empty cart to avoid breaking the UI
      dispatch({ type: CART_ACTIONS.SET_ITEMS, payload: [] });
      // Sync Zustand cart with empty array
      import('@/store/cartStore').then((module) => {
        module.useCartStore.getState().clearCart();
      });
    } finally {
      setLoading(false);
    }
  };

  // Load cart from localStorage based on login state
  useEffect(() => {
    if (!userId) {
      // User is not logged in, set cart to empty to ensure it appears empty in the UI
      const savedCartItems = JSON.parse(localStorage.getItem('cartItems') || '[]');
      dispatch({ type: CART_ACTIONS.SET_ITEMS, payload: savedCartItems });
    } else {
      // User is logged in, fetch their cart from the server
      fetchItems();
    }
  }, [userId]);

  // Sync Zustand cart with CartContext cart
  useEffect(() => {
    // Initialize Zustand cart from CartContext
    import('@/store/cartStore').then((module) => {
      const { items } = module.useCartStore.getState();
      if (items.length === 0 && state.items.length > 0) {
        // If Zustand store is empty but CartContext has items, populate Zustand
        state.items.forEach(item => {
          module.useCartStore.getState().addItem(item);
        });
      } else if (items.length > 0 && state.items.length === 0) {
        // If Zustand has items but CartContext is empty, populate CartContext
        dispatch({ type: CART_ACTIONS.SET_ITEMS, payload: items });
      }
    });
  }, [state.items]);

  // Save cart to localStorage only when not logged in
  useEffect(() => {
    // Only save guest cart to localStorage if not logged in
    if (!userId) {
      localStorage.setItem('cartItems', JSON.stringify(state.items));
    }
  }, [state.items, userId]);

  const setItems = (items: CartItem[]) => {
    dispatch({ type: CART_ACTIONS.SET_ITEMS, payload: items });
  };

  const setLoading = (loading: boolean) => {
    dispatch({ type: CART_ACTIONS.SET_LOADING, payload: loading });
  };

  const setError = (error: string | null) => {
    dispatch({ type: CART_ACTIONS.SET_ERROR, payload: error });
  };

  // Add item to cart
  const addToCart = async (product: any, quantity = 1) => {
    // Prevent multiple requests for the same product
    if (ongoingCartRequests.has(product.id)) {
      return; // Skip if already processing a request for this product
    }

    if (userId) {
      ongoingCartRequests.add(product.id); // Mark this product as having an ongoing request
      setLoading(true);
      setError(null);

      try {
        const response = await apiClient.addToCart(product.id, quantity);
        if (response.success && response.data) {
          dispatch({ type: CART_ACTIONS.ADD_ITEM, payload: response.data });
          // Sync with Zustand store
          import('@/store/cartStore').then((module) => {
            module.useCartStore.getState().addItem(response.data);
          });
        } else {
          throw new Error(response.error || 'Failed to add item to cart');
        }
      } catch (error: any) {
        console.error('Error adding to cart:', error);
        setError(error.message || 'Failed to add item to cart. Please try again.');
      } finally {
        ongoingCartRequests.delete(product.id); // Remove the tracking
        setLoading(false);
      }
    } else {
      // For non-authenticated users, add to Zustand cart directly
      const cartItem = {
        id: Date.now(), // Temporary ID
        product_id: product.id,
        quantity,
        name: product.name,
        price: product.price,
        image_url: product.image_url,
      };

      import('@/store/cartStore').then((module) => {
        module.useCartStore.getState().addItem(cartItem);
      });

      // Update context state
      const existingItem = state.items.find(item => item.product_id === product.id);
      if (existingItem) {
        dispatch({ 
          type: CART_ACTIONS.UPDATE_ITEM, 
          payload: { product_id: product.id, quantity: existingItem.quantity + quantity } 
        });
      } else {
        dispatch({ type: CART_ACTIONS.ADD_ITEM, payload: cartItem });
      }
    }
  };

  // Update cart item
  const updateItem = async (productId: number, quantity: number) => {
    // Only proceed if product ID is valid
    if (!productId || typeof productId !== 'number' || productId <= 0) {
      console.error('Invalid product ID provided to updateItem:', productId);
      return;
    }

    if (userId) {
      setLoading(true);
      setError(null);

      try {
        const response = await apiClient.updateCartItem(productId, quantity);
        if (response.success && response.data) {
          // Use ADD_ITEM action to properly handle the full cart item data from API
          dispatch({ type: CART_ACTIONS.ADD_ITEM, payload: response.data });
          // Sync with Zustand store
          import('@/store/cartStore').then((module) => {
            module.useCartStore.getState().updateItem(productId, quantity);
          });
        } else if (quantity <= 0 && response.success) {
          // When quantity is 0, the item was removed
          dispatch({ type: CART_ACTIONS.REMOVE_ITEM, payload: productId });
          // Sync with Zustand store
          import('@/store/cartStore').then((module) => {
            module.useCartStore.getState().removeItem(productId);
          });
        } else {
          throw new Error(response.error || 'Failed to update cart item');
        }
      } catch (error: any) {
        console.error('Error updating cart item:', error);
        setError(error.message || 'Failed to update cart item. Please try again.');
      } finally {
        setLoading(false);
      }
    } else {
      if (quantity <= 0) {
        dispatch({ type: CART_ACTIONS.REMOVE_ITEM, payload: productId });
        // Sync with Zustand store
        import('@/store/cartStore').then((module) => {
          module.useCartStore.getState().removeItem(productId);
        });
      } else {
        dispatch({ type: CART_ACTIONS.UPDATE_ITEM, payload: { product_id: productId, quantity } });
        // Sync with Zustand store
        import('@/store/cartStore').then((module) => {
          module.useCartStore.getState().updateItem(productId, quantity);
        });
      }

      // Update localStorage for guest cart
      const currentCart = JSON.parse(localStorage.getItem('cartItems') || '[]');
      const existingItemIndex = currentCart.findIndex((item: any) => item.product_id === productId);

      if (existingItemIndex !== -1) {
        if (quantity <= 0) {
          // Remove item from localStorage
          currentCart.splice(existingItemIndex, 1);
        } else {
          // Update quantity in localStorage
          currentCart[existingItemIndex].quantity = quantity;
        }
      } else if (quantity > 0) {
        // Find the product to get its details and add to localStorage
        // For guest cart, we'll use the current cart state to find product details
        const existingItem = state.items.find(p => p.product_id === productId);
        if (existingItem) {
          currentCart.push({
            id: existingItem.id,
            product_id: existingItem.product_id,
            name: existingItem.name,
            price: existingItem.price,
            quantity,
            image_url: existingItem.image_url,
            user_id: null
          });
        }
      }

      localStorage.setItem('cartItems', JSON.stringify(currentCart));
    }
  };

  // Remove item from cart
  const removeItem = async (productId: number) => {
    // Only proceed if product ID is valid
    if (!productId || typeof productId !== 'number' || productId <= 0) {
      console.error('Invalid product ID provided to removeItem:', productId);
      return;
    }

    if (userId) {
      setLoading(true);
      setError(null);

      try {
        const response = await apiClient.removeFromCart(productId);
        if (response.success) {
          dispatch({ type: CART_ACTIONS.REMOVE_ITEM, payload: productId });
          // Sync with Zustand store
          import('@/store/cartStore').then((module) => {
            module.useCartStore.getState().removeItem(productId);
          });
        } else {
          throw new Error(response.error || 'Failed to remove item from cart');
        }
      } catch (error: any) {
        console.error('Error removing from cart:', error);
        setError(error.message || 'Failed to remove item from cart. Please try again.');
      } finally {
        setLoading(false);
      }
    } else {
      dispatch({ type: CART_ACTIONS.REMOVE_ITEM, payload: productId });
      // Sync with Zustand store
      import('@/store/cartStore').then((module) => {
        module.useCartStore.getState().removeItem(productId);
      });

      // Update localStorage for guest cart
      const currentCart = JSON.parse(localStorage.getItem('cartItems') || '[]');
      const updatedCart = currentCart.filter((item: any) => item.product_id !== productId);
      localStorage.setItem('cartItems', JSON.stringify(updatedCart));
    }
  };

  // Clear cart
  const clearCart = async () => {
    if (userId) {
      setLoading(true);
      setError(null);

      try {
        const response = await apiClient.clearCart();
        if (response.success) {
          dispatch({ type: CART_ACTIONS.CLEAR_CART });
          // Sync with Zustand store
          import('@/store/cartStore').then((module) => {
            module.useCartStore.getState().clearCart();
          });
        } else {
          throw new Error(response.error || 'Failed to clear cart');
        }
      } catch (error: any) {
        console.error('Error clearing cart:', error);
        setError(error.message || 'Failed to clear cart. Please try again.');
      } finally {
        setLoading(false);
      }
    } else {
      dispatch({ type: CART_ACTIONS.CLEAR_CART });
      // Sync with Zustand store
      import('@/store/cartStore').then((module) => {
        module.useCartStore.getState().clearCart();
      });

      // Update localStorage for guest cart
      localStorage.setItem('cartItems', JSON.stringify([]));
    }
  };

  return (
    <CartContext.Provider
      value={{
        items: state.items,
        loading: state.loading,
        error: state.error,
        setItems,
        setLoading,
        setError,
        fetchItems,
        addToCart,
        updateItem,
        removeItem,
        clearCart,
        cartCount,
        cartTotal
      }}
    >
      {children}
    </CartContext.Provider>
  );
};

export const useCart = () => {
  const context = useContext(CartContext);
  if (context === undefined) {
    throw new Error('useCart must be used within a CartProvider');
  }
  return context;
};